import { useHashParams } from "@rp/react/web/common/hooks"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { PullToRefresh } from "./PullToRefresh"

const PwaPullToRefresh = ({ isSearchBarOpen }: { isSearchBarOpen: boolean }) => {
  const { isIOS, isPWA } = useDeviceInfo()
  const [documentParams] = useHashParams()
  if (isIOS && isPWA && !isSearchBarOpen && !documentParams["document"]) return <PullToRefresh />
  return null
}

export { PwaPullToRefresh }
